@use "global" as *;
.page-head {
    display: grid;
    place-items: center;
    height: rem(460);
    @include mq(md) {
        height: rem(548);
    }
}
.page-head__title {
    font-size: rem(40);
    font-family: $english-font-family;
    font-weight: 400;
    position: absolute;
    color: $white;
    @include mq(md) {
        font-size: rem(80);
    }
}
.page-head__image {
    overflow: hidden;
}
.page-head__image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
